@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
.slider-outer-container{width:100%;padding-top:10px}.slider-container{width:100%;height:50px}.slider-range-container{position:relative;padding-top:34px}.slider-range-left{position:absolute;top:50%;height:6px;border-top-left-radius:2px;border-bottom-left-radius:2px;left:0;z-index:10}.slider-range-background{position:absolute;top:50%;height:6px;border-radius:2px;background:#aaa;left:0;width:100%;z-index:5}.slider-range-right{position:absolute;top:calc(50% - 3px);height:6px;border-top-right-radius:2px;border-bottom-right-radius:2px;z-index:6}.sliderValue{position:absolute;display:flex;top:50%;height:40px;width:80px;border:1px solid #666;border-radius:2px;text-align:center;transform:translateY(-43%);background:#efefef;color:#666;font-size:14px;z-index:20;-moz-user-select:none;-webkit-user-select:none;box-shadow:5px 5px 5px rgba(0,0,0,0.5);justify-content:center;align-items:center;cursor:move}

body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100%;margin:0;background:#fff;box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}.full-width{width:100%;max-width:100%}.interior-container{width:100%;max-width:1200px;margin:0 auto}.inputs-container{width:100%;min-height:500px;margin-top:10px}.inputs-container-top{background:#f5f3f5;color:#444;padding:5px;font-weight:500}.header{color:#01233f;height:60px;display:flex;align-items:center;border-top:5px solid #64a3e3}.header-title{font-size:24px;font-family:"Poppins";font-weight:600;padding-left:5px}.header-logo{width:30px;height:30px;background:url(/static/media/calculator.31ec2823.png);background-repeat:no-repeat;background-size:contain}.flex-row{display:flex;flex-direction:row}.flex-col-40{width:40%}.flex-col-60{width:60%}.inputs-row{width:100%;display:flex;flex-direction:row;background:#fbfbfb;margin-top:10px;position:relative;border-left:10px solid #fbfbfb}.inputs-row-title{font-weight:600;color:#666}.inputs-row:hover{background:#eee;border-left:10px solid #64a3e3}.inputs-row-body{padding:25px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.caret-container{height:20px;width:20px;position:absolute;top:calc(50% - 10px);left:0px}.caret{position:relative}.caret:before{content:"";position:absolute;top:0;left:0;border-left:10px solid #64a3e3;border-top:10px solid transparent;border-bottom:10px solid transparent}.caret:after{content:"";position:absolute;left:0;top:1px;border-left:9px solid #64a3e3;border-top:9px solid transparent;border-bottom:9px solid transparent}.fade-in{opacity:0}.fade-in.visible{transition:opacity 3s ease-in;opacity:1}

