@import "./theme.scss";

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  margin: 0;
  background: $bodyBackground;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.full-width {
  width: 100%;
  max-width: 100%;
}

.interior-container {
  width: 100%;
  max-width: $maxWidth;
  margin: 0 auto;
}

.inputs-container {
  width: 100%;
  min-height: 500px;
  margin-top: 10px;
  &-top {
    background: $offsetBGColor;
    color: $offsetFontColor;
    padding: 5px;
    font-weight: 500;
  }
}

.header {
  color: $primaryColor;
  height: 60px;
  display: flex;
  align-items: center;
  border-top: 5px solid $secondaryColor;
}

.header-title {
  font-size: $headerTitleSize;
  font-family: $headerFont;
  font-weight: 600;
  padding-left: 5px;
}

.header-logo {
  width: $logoSize;
  height: $logoSize;
  background: url("../images/calculator.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-col-40 {
  width: 40%;
}

.flex-col-60 {
  width: 60%;
}

.inputs-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  background: $calculatorRowBackground;
  margin-top: 10px;
  position: relative;
  // -moz-transition: all 0.2s ease-in;
  // -o-transition: all 0.2s ease-in;
  // -webkit-transition: all 0.2s ease-in;
  border-left: 10px solid $calculatorRowBackground;

  &-title {
    font-weight: 600;
    color: $calculatorRowTitleColor;
  }
  &:hover {
    background: $calculatorRowHoverColor;
    border-left: 10px solid $secondaryColor;
  }
  &-body {
    padding: 25px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.caret-container {
  height: 20px;
  width: 20px;
  position: absolute;
  top: calc(50% - 10px);
  left: 0px;
}

.caret {
  position: relative;
}

.caret:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-left: 10px solid $secondaryColor;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}

.caret:after {
  content: "";
  position: absolute;
  left: 0;
  top: 1px;
  border-left: 9px solid $secondaryColor;
  border-top: 9px solid transparent;
  border-bottom: 9px solid transparent;
}

.fade-in {
  opacity: 0;
}
.fade-in.visible {
  transition: opacity 3s ease-in;
  opacity: 1;
}
