@import "config.scss";

.slider-outer-container {
  width: 100%;
  padding-top: 10px;
}

.slider-container {
  width: 100%;
  height: 50px;
}

.slider-range-container {
  position: relative;
  padding-top: 34px;
}

.slider-range-left {
  position: absolute;
  top: 50%;
  height: 6px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  left: 0;
  z-index: 10;
}

.slider-range-background {
  position: absolute;
  top: 50%;
  height: 6px;
  border-radius: 2px;
  background: $mediumGray;
  left: 0;
  width: 100%;
  z-index: 5;
}

.slider-range-right {
  position: absolute;
  top: calc(50% - 3px);
  height: 6px;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  z-index: 6;
}

.sliderValue {
  position: absolute;
  display: flex;
  top: 50%;
  height: 40px;
  width: 80px;
  border: 1px solid $darkGray;
  border-radius: 2px;
  text-align: center;
  transform: translateY(-43%);
  background: $softWhite;
  color: $darkGray;
  font-size: 14px;
  z-index: 20;
  -moz-user-select: none;
  -webkit-user-select: none;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  cursor: move;
}
